import React,{useState} from 'react'

export default function TextForm(props) {

const speak = () => {
    let msg = new SpeechSynthesisUtterance();
    msg.text = text;
    window.speechSynthesis.speak(msg);
  }

const handleUpClick =()=>{
  console.log("Uppercase button was clicked");
  let newText = text.toUpperCase()
  setText(newText)
}

const handleLowClick = ()=>{
  let newText = text.toLowerCase()
  setText(newText)
}
const handleOnChange=(event)=>{
  console.log("On Change")
  setText(event.target.value);
}

const handleClear= ()=>{
  let newText = ""
  setText(newText)
}

const handleCopy = ()=>{
  let text = document.getElementById("myBox")
  text.select()
  navigator.clipboard.writeText(text.value)
  props.showAlert("Text has been copied","success")
}

const handleRES =()=>{
  let newText = text.split(/[ ]+/)
  setText(newText.join(" "))
}

const [text,setText] = useState("Enter text here")

  return (
    <>
    <div className="container" style={{backgroundColor:props.mode ==="dark"?"grey":"white",color:props.mode ==="light"?"black":"white"}}>
    <div >
        <h1>{props.heading}</h1>
        <div className="mb-3">

        <textarea className="form-control" value= {text} onChange = {handleOnChange} style={{backgroundColor:props.mode ==="dark"?"#395e7e":"white",color:props.mode ==="dark"?"white":"black"}} id="myBox" rows="8"></textarea>
        </div>
        <button className="btn btn-primary mx-1" onClick={handleUpClick}>Covert to UpperCase</button>
        <button className="btn btn-primary mx-1" onClick={handleLowClick}>Covert to lowercase</button>
        <button type="submit" onClick={speak} className="btn btn-warning mx-2 my-2">Speak</button>
        <button className="btn btn-success mx-1" onClick={handleClear}>Clear</button>
        <button className="btn btn-info mx-1" onClick={handleCopy}>Copy</button>
        <button className="btn btn-info mx-1" onClick={handleRES}>Remove extra spaces</button>
    </div>
    <div className="container my-3" >
      <h2><u>Below is the summary</u></h2>
      <p>It contains <b>{text.replace(/\n/g, " ").split(' ').filter(value => value !== "").length} words</b> and <b>{text.trim().length} characters</b></p>
        <p>It Contains <b>{text.split(/[.?!]\s/).length-1} Sentences</b> and <b>{text.split(/\r\n|\r|\n/).length - 1} Paragraphs</b></p>
        <p>You can Read it in <b>{0.008 * text.trim().length} Minutes</b></p>
      <h3><u>Preview</u></h3>
      <p>{text.length>0?text:"Enter something in text area to view it here"}</p>
    </div>
    </div>
    </>
  )
}
