import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom'

export default function Navbar(props) {
  return (
    <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
  {/* <Link className="navbar-brand" to="/TextForm.js">{props.title}</Link> */}
  <a className="navbar-brand" href="/TextForm.js">{props.title}</a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav mr-auto">
      {/* <li className="nav-item active"> */}
        {/* <Link className="nav-link" to="/About.js">{props.About} <span className="sr-only">(current)</span></Link> */}
        {/* <a className="nav-link" href="/About.js">{props.About} <span className="sr-only">(current)</span></a> */}
      {/* </li> */}
      <li className="nav-item">
        {/* <Link className="nav-link" to="/TextForm.js">{props.link}</Link> */}
        <a className="nav-link" href="/TextForm.js">{props.link}</a>
      </li>
      {/* <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          {props.RollDown}
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          <a className="dropdown-item" href="#">Action</a>
          <a className="dropdown-item" href="#">Another action</a>
          <div className="dropdown-divider"></div>
          <a className="dropdown-item" href="#">Something else here</a>
        </div>
      </li> */}
      <li className="nav-item">
        <a className="nav-link disabled" href="#" tabIndex="-1" aria-disabled="true">{props.hidden}</a>
      </li>
    </ul>
    <div className={`form-check form-switch text-${props.mode ==="light"?"dark":"light"}`}>
  <input className="form-check-input" onClick={props.toggleMode} type="checkbox" id="flexSwitchCheckDefault"/>
  <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Dark Mode</label>
</div>
    {/* <form className="form-inline mx-4 my-lg-0">
      <input className="form-control mr-sm-2"  type="search" placeholder="Search" aria-label="Search"/>
      <button className="btn btn-outline-success my-2 my-sm-0" type="submit">{props.look}</button>
    </form> */}
  </div>
</nav>
  )
}

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    About: PropTypes.string,
    link: PropTypes.number
}
Navbar.defaultProps = {
    title: "Set Title Here",
    link: 'Set option here'
 }