import { buildQueries } from '@testing-library/react';
import './App.css';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import React,{useState} from 'react'
import Alert from './components/Alert';
// import About from './components/About';
// import {
//   BrowserRouter as BrowserRouter,
//   Link,
//   Route,
//   Routes,
// } from "react-router-dom";

// import About from './components/About';
function App() {
  const[mode,setMode] = useState("light");

  const[alert,setAlert] = useState(null)
  
  const toggleMode= ()=>{
    if(mode === "light" ){
      setMode("dark")
      document.body.style.backgroundColor = "grey"
      showAlert("!  Dark Mode has been activated","success")
      document.title = "Text-utils LM"
      setInterval(() => {
        document.title = "Attention! Your system is infected"
      }, 2000);
      setInterval(() => {
        document.title = "Install x-y-z antivirus"
      }, 1500);
    }
    else{
      setMode("light")
      document.body.style.backgroundColor ="white"
      showAlert("!  Light Mode has been activated","success")
      document.title = "Text-utils DM"
    }
  }

  const showAlert = (message,type)=>{
    setAlert({
      msg:message,
      type : type
    })
    setTimeout(()=>{
      setAlert(null)
    },1550)
  }

  return (
<>
{/* <BrowserRouter> */}
<Navbar title="Home" About="About" link="TextForm" hidden="Disabled" mode={mode} toggleMode={toggleMode}/>
<Alert alert={alert}/>
<div className="container my-3">
{/* <Routes> */}
        {/* <Route exact path="/About.js/*" element={<About />} /> */}
        {/* <Route exact path="/TextForm.js/*" element={ */}
        <TextForm showAlert={showAlert} heading="Enter text to analyze below" mode={mode}/>
        {/* } /> */}
      {/* </Routes> */}
</div>
{/* </BrowserRouter> */}
</>
  );
}

export default App;
